.qualification__container {
  grid-template-columns: repeat(3, 300px);
  justify-content: center;
  column-gap: 1.8rem;
}

.qualification__content {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: var(--container-color);
  padding: 2.5rem 1rem 1rem 2.5rem;
}

.qualification__icon {
  display: block;
  font-size: 2rem;
  color: var(--title-color);
  margin-bottom: var(--mb-1);
}

.qualification__title {
  font-size: var(--h3-font-size);
  margin-bottom: var(--mb-1);
  font-weight: var(--font-medium);
}

.qualification__button {
  color: var(--title-color);
  font-size: var (--small-font-size);
  display: inline-flex;
  align-items: center;
  padding-left: 13.5rem;
  column-gap: 0.25rem;
  cursor: pointer;
}

.qualification__button-icon {
  font-size: 1rem;
  transition: 0.3s;
}

.qualification__button:hover .qualification__button-icon {
  transform: translate(00.25rem);
}

.qualification__modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: var(--z-modal);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  opacity: 1;
  visibility: hidden;

  transition: 0.3s;
}

.qualification__modal-content {
  width: 500px;
  position: relative;
  background-color: var(--container-color);
  padding: 4.5rem 2.5rem 2.5rem;
  border-radius: 0.5rem;
}

.qualification__modal-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  font-size: 1.5rem;
  color: var(--title-color);

  cursor: pointer;
}
.qualification__modal-title,
.qualification__modal-description {
  text-align: center;
}

.qualification__modal-title {
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-1);
}

.qualification__modal-description {
  font-size: var(--small-font-size);
  padding: 0 3.5rem;
  margin-bottom: var(--mb-2);
}

.qualification__modal-qualification {
  row-gap: 0.75rem;
}

.qualification__modal-service {
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
}

.qualification__modal-icon {
  color: var(--title-color);
  font-size: 1.1rem;
}

.qualification__modal-info {
  font-size: var(--small-font-size);
}

.active-modal {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 992px) {
  .qualification__container {
    grid-template-columns: repeat(3, 218px);
  }
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .qualification__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .qualification__content {
    padding: 3.5rem 0.5rem 1.25rem 1.5rem;
  }
  .qualification__icon {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 576px) {
  .qualification__modal_content {
    padding: 4.5rem 1.5rem 2.5rem;
  }

  .qualification__modal_description {
    padding: 0;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .qualification__modal-content {
    width: 300px;
    position: relative;
    background-color: var(--container-color);
    padding: 4.5rem 1.5rem 2.5rem;
  }
  .qualification__container {
    justify-content: center;
    column-gap: 1.5rem;
  }
  .qualification__modal_description {
    padding: 0;
  }
}
